<template>
  <div class="cartitem" v-if="item.quantity > 0">
    <div class="cartitem__wrap">
      <img :src="convertImageUrl(item.image.url)" alt="" class="cartitem__img" />
      <div class="cartitem__desc">
        <div class="cartitem__main">
          <h1 class="cartitem__h">{{ item.name }}</h1>
          <p class="cartitem__price">{{ item.price.formatted }}0 Сум</p>
        </div>
        <div class="cartitem__count">
          <div class="cartitem__counter">
            <button
              class="cartitem__counter-plus"
              @click="addToCart({ productId: item.product_id, quantity: 1 })"
              :disabled="addingToCart === item.product_id"
            >
            +
              <span v-if="addingToCart !== item.product_id"></span>
              <div v-else class="load"></div>
            </button>
            <p class="cartitem__counter-p">{{ item.quantity }}</p>
            <button
              class="cartitem__counter-minus"
              @click="addToCart({ productId: item.product_id, quantity: -1 })"
              :disabled="addingToCart === item.product_id"
            >
            -
            <span v-if="addingToCart !== item.product_id"></span>
              <div v-else class="load"></div>
            </button>
          </div>
          <button
            class="cartitem__count-remove"
            @click="
              addToCart({
                productId: item.product_id,
                quantity: -item.quantity,
              })
            "
          >
          Удалить
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
export default {
  props: ["item"],
  methods: {
    ...mapActions(["addToCart"]),
    convertImageUrl(url) {
      return url.replace(/\|/g, '%7C');
    },
  },
  computed: {
    ...mapState(["addingToCart"]),
  },
};
</script>

<style>
.load {
  background: rgba(0, 0, 0, 0.3);
  overflow: hidden;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10000;
}
</style>
