<template>
  <section class="cart">
    <div class="container">
      <h1 class="cart__title">Ваша Корзина</h1>
      <div class="cart__wrap" v-if="cart">
        <Cart v-for="item in cart.line_items" :key="item.id" :item="item" />
      </div>
      <div class="loading-overlay" v-else>
        <div class="loading-circle"></div>
      </div>
      <div class="cart__checkout" v-if="cart && cart.total_items > 0">
        <p class="cart__checkout-subtotal">
          Итого: {{ cart.subtotal.formatted }}0 Сум
        </p>
        <div class="cart__checkout-main">
          <button
            class="cart__checkout-main-emptyBtn"
            @click="clearCart"
            :disabled="loading"
          >
            Пустая корзина
            <span v-if="loading" class="load"></span>
          </button>
          <router-link to="/checkout" class="cart__checkout-main-checkoutBtn"
            >Оформить заказ</router-link
          >
        </div>
      </div>
      <div class="cart__empty" v-else-if="cart && cart.total_items <= 0">
        <h2 class="cart__empty-h">Ваша корзина на данный момент пуста</h2>
        <img class="cart__empty-img" src="@/assets/img/empty-cart.png" alt="" />
        <router-link to="/main" class="cart__empty-btn"
          >Продолжить покупки</router-link
        >
      </div>
    </div>
  </section>
</template>

<script>
import Cart from "@/components/Cart/Cartitem.vue";
import { mapState, mapActions } from "vuex";
export default {
  components: {
    Cart,
  },
  computed: {
    ...mapState(["cart", "loading"]),
  },
  created() {
    this.getCart();
  },
  methods: {
    ...mapActions(["getCart", "clearCart"]),
  },
};
</script>

<style></style>
